
body,html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root{
  height: 100%;
  width: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body { 
  overflow-y: visible !important
}
/** Color **/
/** Media **/
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background: -webkit-linear-gradient(top, #550009, #000000);
  background: linear-gradient(to bottom, #550009, #000000);
  color: white;
  font-family: 'Varela Round', sans-serif; }

.main-page {
  height: 100%;
  width: 100%; }

/** Color **/
/** Media **/
.movie-display {
  display: flex;
  position: relative;
  flex-direction: row;
  flex-wrap: wrap;
  background: -webkit-linear-gradient(top, #550009, #140002);
  background: linear-gradient(to bottom, #550009, #140002);
  justify-content: center;
  align-items: center;
  padding: 10px 15px 0 15px; }

@media only screen and (max-width: 550px) {
  padding: 10px 5px 0 5px; }

/** Color **/
/** Media **/
.movie-container {
  display: flex;
  flex-direction: row;
  height: 330px;
  width: 509px;
  margin: 10px; }

.movie-picture {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  width: 50%;
  background-size: cover;
  background-position: 50%;
  border: 0px solid black;
  border-top-left-radius: 2%;
  border-bottom-left-radius: 2%;
  overflow: hidden; }

.movie-info-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 5px 10px;
  margin-top: 10px; }

.movie-info {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 60%;
  background-color: rgba(0, 0, 0, 0.329412);
  font-size: 14px;
  border-top-right-radius: 2%;
  border-bottom-right-radius: 2%; }

.movie-title-year {
  font-size: 18px;
  font-weight: bold; }

.movie-info-text {
  padding: 0 10px 0 10px; }

.movie-runtime-genre {
  padding-top: 5px; }

.movie-desc {
  padding: 15px 10px 10px;
  overflow: hidden;
  text-overflow: ellipsis; }

.movie-director-title {
  font-weight: bold;
  letter-spacing: 1px; }

@media only screen and (max-width: 550px) {
  .movie-container {
    margin: 7px 0;
    height: 225px;
    width: 330px; }
  .movie-info {
    font-size: 11px; }
  .movie-title-year {
    font-size: 13px; }
  .movie-desc {
    padding: 5px 10px 0 10px; }
  .movie-director {
    padding: 5px 10px; } }

/** Color **/
/** Media **/
.movie-options {
  cursor: pointer;
  font-size: 18px; }
  .movie-options .fas {
    padding: 0 7px; }

@media only screen and (max-width: 550px) {
  .movie-options {
    font-size: 15px; } }

/** Color **/
/** Media **/
.header {
  width: 100%;
  height: 80%; }

.header-background {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-image: url(/cinema-home-assignment/static/media/cinema_header.b151afb4.png);
  background-size: cover;
  background-position: 50%; }
  .header-background::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: -webkit-linear-gradient(transparent 70%, #550009);
    background: linear-gradient(transparent 70%, #550009); }

.header-info {
  width: 100%;
  height: 35%;
  background: rgba(0, 0, 0, 0.278431);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; }

.header-title {
  text-align: center;
  color: white;
  text-shadow: 4px 3px 2px #635101;
  font-family: 'Righteous', cursive;
  font-size: 140px; }

.header-description {
  text-align: center;
  color: white;
  text-shadow: 2px 1px 2px #635101;
  font-size: 20px;
  font-weight: bold; }

.top-header {
  display: flex;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.84);
  height: 50px;
  width: 100%;
  top: 0;
  justify-self: flex-end;
  z-index: 1; }

.header-link-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 20%;
  height: 100%; }

.header-link-text {
  text-align: center;
  z-index: 1;
  -webkit-transition: color 0.4s linear;
  transition: color 0.4s linear;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.header-link-btn {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: flex-start;
  padding: 0 20px 0 20px;
  cursor: pointer;
  height: 100%; }
  .header-link-btn:hover .header-link-text {
    color: #c09d00; }

@media only screen and (max-width: 550px) {
  .header {
    height: 60%; }
  .header-link-btn {
    padding: 0px 10px 0 20px;
    font-size: 14px; }
  .header-title {
    font-size: 85px;
    height: 70%; }
  .header-description {
    font-size: 14px; }
  .header-link-container {
    width: 30%;
    justify-content: center; } }

.add-movie-form, .add-movie-description {
  margin: 0 40px 0 40px !important; }

.add-movie-title {
  padding-bottom: 10px !important; }

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: black;
  height: 130px;
  width: 100%; }

.footer-text {
  font-size: 14px;
  padding-left: 50px; }

